.wishlist {
  margin: 0 auto;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}

h2 {
  text-align: center;
  text-transform: capitalize;
  color: #2e3440;
}

ul {
  display: block;
  width: 800px;
}

li {
  padding: 5px;
  padding-left: 15px;
  margin-bottom: 10px;
  list-style: none;
  border: 1px solid #81a1c1;
  display: flex;
  justify-content: space-between;
}

li > button {
  width: 12em;
}

li > a {
  text-decoration: none;
}

.active, .active a {
  color: #eceff4;
  background: #5e81ac;
}

.active button {
  color: #3b4252;
  background: #eceff4;
}

.notactive {
  color: #3b4252;
  background: #eceff4;
}

.notactive button {
  color: #eceff4;
  background: #5e81ac;
}

button {
  padding: 5px;
  border: 0;
  cursor: pointer;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, .25);
}

button:hover {
  box-shadow: 1px 2px 3px rgba(0, 0, 0, .15), -1px 2px 3px rgba(0, 0, 0, .15);
}

button:focus {
  outline: 0;
}
